:root {
    font-size: 10px;
    --color-bg: #ffffff;
    --color-red-500: #E89F9F;
    --color-red-700: #D77171;
    --color-blue-500: #7ECDE5;
    --color-blue-700: #50B3D2;
    --color-gray-50: #F5F5F5;
    --color-gray-100: #EAEAEA;
    --color-gray-200: #999999;
    --color-gray-500: #666666;
    --color-gray-700: #333333;
    --font-size-2xl: 3.2rem;
    --font-size-xl: 2.4rem;
    --font-size-lg: 1.8rem;
    --font-size-base: 1.6rem;
    --font-size-sm: 1.4rem;
    --font-size-xs: 1.2rem;
}

html {
    font-family: "Roboto", "Noto Sans TC", "Noto Sans SC", "微軟正黑體", "Microsoft JhengHei";
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-weight: 500;
    &.is-fixed {
        overflow: hidden;
    }
}

body {
    background-color: var(--color-bg);
}

.site-main {
    box-sizing: border-box;
    max-width: 48rem;
    margin: 0 auto;
    padding: 0 2.4rem;
}