.section-result {
    margin-bottom: 3.2rem;
    padding-bottom: 3.2rem;
    border-bottom: 0.1rem dotted var(--color-gray-100);
    &__title {
        display: flex;
        justify-content: center;
        margin-bottom: 1.6rem;
        span {
            padding: 0.8rem 2rem;
            border-radius: 3rem;
            border: solid 0.2rem var(--color-gray-500);
            font-size: var(--font-size-lg);
            line-height: 1.6;
            text-align: center;
            color: var(--color-gray-500);
        }
    }
    &__text {
        line-height: 1.6;
        color: var(--color-gray-500);
        font-size: var(--font-size-sm);
    }
}