.section-intro {
    padding-bottom: 4rem;
    &__block {
        &:not(:last-child) {
            margin-bottom: 3.2rem;
        }
    }
    &__title {
        font-size: var(--font-size-lg);
        position: relative;
        padding-left: 2rem;
        line-height: 1.6;
        color: var(--color-gray-500);
        margin-bottom: 0.8rem;
        &::before {
            width: 1.6rem;
            height: 1.6rem;
            position: absolute;
            left: 0;
            top: 0.5rem;
            border-radius: 50%;
            content: "";
            background-color: var(--color-gray-200);
        }
        &.is-red {
            &::before {
                background-color: var(--color-red-500);
            }
        }
        &.is-blue {
            &::before {
                background-color: var(--color-blue-500);
            }
        }
    }
    &__text {
        line-height: 1.6;
        color: var(--color-gray-500);
        font-size: var(--font-size-sm);
        padding-left: 2rem;
    }
}